import React from "react";
import "./styles.scss";
import { useAuth0 } from "@auth0/auth0-react";
function Profile() {
  const { isAuthenticated, user } = useAuth0();
  return (
    isAuthenticated && (
      <div className="profile">
        <img src={user.picture} alt="" />
        <span>{user.name}</span>
      </div>
    )
  );
}

export default Profile;
