import "./App.scss";
import React from "react";
import axios from "axios";
import Side from "./components/Side";
import { Outlet } from "react-router-dom";
import { useState } from "react";
import { createContext } from "react";
import { useAuth0 } from "@auth0/auth0-react";
export const AppContext = createContext({});

function App() {
  const [transactions, setTransactions] = useState([]);
  const { isAuthenticated, user, isLoading } = useAuth0();
  React.useEffect(() => {
    isAuthenticated &&
      axios
        .get("https://6738aa0b4eb22e24fca89960.mockapi.io/Transactions")
        .then((res) => {
          setTransactions(res.data.filter((item) => item.user_id === user.sub));
        })
        .catch((error) => {
          console.error("Error fetching transactions:", error);
        });
  }, [isAuthenticated, user]);
  if (isLoading) {
    return <div>Loading...</div>;
  }
  return (
    <AppContext.Provider value={{ transactions, setTransactions }}>
      <div className="App">
        <Side />
        <Outlet context={{ transactions, setTransactions }} />
      </div>
    </AppContext.Provider>
  );
}

export default App;
